import { createReducer, createActions } from 'reduxsauce';
import Immutable from 'seamless-immutable';
import { loadMoreDataWhenScrolled } from '../../../shared/util/pagination-utils';
import { parseHeaderForLinks } from '../../../shared/util/url-utils';

/* ------------- Types and Action Creators ------------- */

const { Types, Creators } = createActions({
  bidRequest: ['bidId'],
  bidAllRequest: ['options'],
  bidUpdateRequest: ['bid'],
  bidDeleteRequest: ['bidId'],

  bidSuccess: ['bid'],
  bidAllSuccess: ['bidList', 'headers'],
  bidUpdateSuccess: ['bid'],
  bidDeleteSuccess: [],

  bidFailure: ['error'],
  bidAllFailure: ['error'],
  bidUpdateFailure: ['error'],
  bidDeleteFailure: ['error'],

  bidReset: [],
});

export const BidTypes = Types;
export default Creators;

/* ------------- Initial State ------------- */

export const INITIAL_STATE = Immutable({
  fetchingOne: false,
  fetchingAll: false,
  updating: false,
  deleting: false,
  updateSuccess: false,
  bid: { id: undefined },
  bidList: [],
  errorOne: null,
  errorAll: null,
  errorUpdating: null,
  errorDeleting: null,
  links: { next: 0 },
  totalItems: 0,
});

/* ------------- Reducers ------------- */

// request the data from an api
export const request = (state) =>
  state.merge({
    fetchingOne: true,
    errorOne: false,
    bid: INITIAL_STATE.bid,
  });

// request the data from an api
export const allRequest = (state) =>
  state.merge({
    fetchingAll: true,
    errorAll: false,
  });

// request to update from an api
export const updateRequest = (state) =>
  state.merge({
    updateSuccess: false,
    updating: true,
  });
// request to delete from an api
export const deleteRequest = (state) =>
  state.merge({
    deleting: true,
  });

// successful api lookup for single entity
export const success = (state, action) => {
  const { bid } = action;
  return state.merge({
    fetchingOne: false,
    errorOne: null,
    bid,
  });
};
// successful api lookup for all entities
export const allSuccess = (state, action) => {
  const { bidList, headers } = action;
  const links = parseHeaderForLinks(headers.link);
  return state.merge({
    fetchingAll: false,
    errorAll: null,
    links,
    totalItems: parseInt(headers['x-total-count'], 10),
    bidList: loadMoreDataWhenScrolled(state.bidList, bidList, links),
  });
};
// successful api update
export const updateSuccess = (state, action) => {
  const { bid } = action;
  return state.merge({
    updateSuccess: true,
    updating: false,
    errorUpdating: null,
    bid,
  });
};
// successful api delete
export const deleteSuccess = (state) => {
  return state.merge({
    deleting: false,
    errorDeleting: null,
    bid: INITIAL_STATE.bid,
  });
};

// Something went wrong fetching a single entity.
export const failure = (state, action) => {
  const { error } = action;
  return state.merge({
    fetchingOne: false,
    errorOne: error,
    bid: INITIAL_STATE.bid,
  });
};
// Something went wrong fetching all entities.
export const allFailure = (state, action) => {
  const { error } = action;
  return state.merge({
    fetchingAll: false,
    errorAll: error,
    bidList: [],
  });
};
// Something went wrong updating.
export const updateFailure = (state, action) => {
  const { error } = action;
  return state.merge({
    updateSuccess: false,
    updating: false,
    errorUpdating: error,
    bid: state.bid,
  });
};
// Something went wrong deleting.
export const deleteFailure = (state, action) => {
  const { error } = action;
  return state.merge({
    deleting: false,
    errorDeleting: error,
    bid: state.bid,
  });
};

export const reset = (state) => INITIAL_STATE;

/* ------------- Hookup Reducers To Types ------------- */

export const reducer = createReducer(INITIAL_STATE, {
  [Types.BID_REQUEST]: request,
  [Types.BID_ALL_REQUEST]: allRequest,
  [Types.BID_UPDATE_REQUEST]: updateRequest,
  [Types.BID_DELETE_REQUEST]: deleteRequest,

  [Types.BID_SUCCESS]: success,
  [Types.BID_ALL_SUCCESS]: allSuccess,
  [Types.BID_UPDATE_SUCCESS]: updateSuccess,
  [Types.BID_DELETE_SUCCESS]: deleteSuccess,

  [Types.BID_FAILURE]: failure,
  [Types.BID_ALL_FAILURE]: allFailure,
  [Types.BID_UPDATE_FAILURE]: updateFailure,
  [Types.BID_DELETE_FAILURE]: deleteFailure,
  [Types.BID_RESET]: reset,
});
